import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { KTX2Loader } from 'three/examples/jsm/loaders/KTX2Loader.js';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js';
import { parseTextForVariables } from '../../../../helpers/sparql';
import { MeshoptDecoder } from 'three/examples/jsm/libs/meshopt_decoder.module.js';
import {Box3,Cache,Object3D,Vector3, REVISION,Matrix4,BufferGeometry,LoadingManager } from "three";
import {Mesh,MeshDepthMaterial,MeshPhysicalMaterial,MeshStandardMaterial, MeshDistanceMaterial, MeshToonMaterial, MeshPhongMaterial,MeshLambertMaterial } from "three";
import * as THREE from 'three';
import IFCGLBFile from './IFCGLBFile';
import { toInteger } from 'lodash';
import  {mergeBufferGeometries,mergeGeometries} from "three/examples/jsm/utils/BufferGeometryUtils.js";




export class GLBFromSparqlResults
{
    constructor(viewer)
    {
        this.viewer=viewer;
        this.ifcdb=viewer.ifcdb;
        if (this.viewer.props.data==null) return;
        try
        {
            var vars=this.viewer.props.data.head.vars;
            for (var n in vars)
            {
                if (vars[n]=="gltf")
                {
                          this.processSparql();
                }
            }
            

        }
        catch(e){
            console.log(e)
        }
    }

     setupIFCDB()
     {
              
        this.viewer.ifcdb.viewer=this.viewer;
        this.ifcdb.pickGroup=null;
        this.ifcdb.createNewPickGroup();
        this.ifcdb.rootGroup=this.ifcdb.getNewRootGroup();
        this.ifcdb.rootGroup.rotation.x = -Math.PI / 2;
        this.viewer.viewer.scene.add(this.ifcdb.rootGroup);


    // part 2
        this.rootGroup=this.ifcdb.getNewRootGroup();
         this.nodes=[];
         this.color_nodes={};

         if (this.colors==null)
          {
            this.colors=[];
            
            this.colors.push(this.getRandomHexColor());
            this.colors.push(this.getRandomHexColor());
            this.colors.push(this.getRandomHexColor());
            this.colors.push(this.getRandomHexColor());
            this.colors.push(this.getRandomHexColor());
            
          }
         
        
      
     }

    processSparql()
    {

        console.log("processing sparql results");
        if (this.viewer.props.data==null) return;
        this.setupIFCDB();
      
       
       var glb=new IFCGLBFile(this.viewer.ifcdb,null,null,null,true);
       this.glb=glb;
       var me=this;
      
       this.loadedN=0;
       this.processedN=0;
        try{
                var bindings=this.viewer.props.data.results.bindings;
                this.resultsN=bindings.length;
             
                for (var b in bindings)
                {
                    var binding=bindings[b];
                    var gltf = binding.gltf.value;
                    var color = null;
                    try {color= binding.color.value;}catch(e){}
                   
                   //  try {  gltf.color=color;} catch(e){gltf=JSON.parse(gltf); gltf.color=color;}
                        var r=glb.loader.parse(gltf,"",function(arg){me.onLoad(arg);},function(arg){me.onError(arg);});
                   
                    
                }
        }
         catch(e)
         {console.log(e);}

                




    }

     getRandomHexColor() {
        // Generate a random number between 0 and 16777215 (0xFFFFFF in hexadecimal)
        const randomColor = Math.floor(Math.random() * 16777215).toString(16);
      
        // Ensure the color has six digits by padding with zeros if necessary
        return "#" + "0".repeat(6 - randomColor.length) + randomColor;
      }
       convertToHex(str) {
        var hex = '';
        for(var i=0;i<str.length;i++) {
            hex += ''+str.charCodeAt(i).toString(16);
        }
        return hex;
    }

      onError(arg)
      {
        console.log("error parsing gltf");
        this.loadedN++;
        if (this.loadedN==this.resultsN)
        {
            this.onLoadReady();
        }

      }
    
    onLoad(gltf)
    {

       // console.log(gltf.scene.children[0].geometry);
       
          
            var c=this.colors[(toInteger(Math.random()*5)+"")];

            if (gltf.color!=null)
            {
                c=this.convertToHex(gltf.color);
            }

              var o3d=gltf.scene.children[0];
                    this.nodes.push(o3d);
                    var ar =this.color_nodes[c];
                    if (ar==null)
                    {
                        ar=[];
                        this.color_nodes[c]=ar;
                    }
                    ar.push(o3d.geometry);

                    this.loadedN++;
                    if (this.loadedN==this.resultsN)
                    {
                        this.onLoadReady();
                    }
                    this.processedN++;
                    if (this.processedN>999)
                    {
                        console.log("processed 1000 ifc objects in total "+this.loadedN);
                        this.processedN=0;
                    }

      
    }
    getMat1(c)
    {
        var mat= new MeshPhongMaterial({shininess: 50,emissiveIntensity:0.4,lightMapIntensity:0.4,emissive:c});
        mat.vertexColors=false;//bijna zeker
        mat.fog=false;
       // mat.alphaHash = true
        mat.side=THREE.DoubleSide;
    }
    getMat(color,hint)
    {


    var mat = new MeshPhysicalMaterial({color:color});
  

    if (hint=="grey")
    {
      mat = new MeshPhysicalMaterial({color:"#c0c0c0"});
      mat.vertexColors=false;//bijna zeker
      mat.fog=true;
     
    }
    if (hint=="greytransparent")
    {
      mat = new MeshPhysicalMaterial({color:"#c0c0c0"});
      mat.vertexColors=false;//bijna zeker
      mat.fog=true;
      mat.transparent=true;
      mat.opacity=0.4;
     
    }
    
    if (hint=="greyverytransparent")
    {
      mat = new MeshPhysicalMaterial({color:"#c0c0c0"});
      mat.vertexColors=false;//bijna zeker
      mat.fog=true;
      mat.transparent=true;
      mat.opacity=0.1;
    
     
    }
  
    if (hint=="nice")
    {
      mat= new MeshPhongMaterial({shininess: 50,emissiveIntensity:0.2,lightMapIntensity:0.4,emissive:color});
      mat.vertexColors=false;//bijna zeker
      mat.fog=true;
     
    }
  
    if (hint=="normal")
    {
     // mat.vertexColors=false;//bijna zeker
      //mat.fog=true;
     
      mat.transparent=true;
      mat.opacity=0.99;
     
   
    }
    if (hint=="transparent")
    {
      mat.transparent=true;
      mat.opacity=0.4;
    }
    if (hint=="vtransparent")
    {
      mat.transparent=true;
      mat.opacity=0.1;
    }
  
    if (hint=="toon")
    {
      mat=new MeshToonMaterial({shininess: 50,emissiveIntensity:0.2,lightMapIntensity:0.4,emissive:color,color:color})
    
    }
    if (hint=="toontransparent")
    {
      mat=new MeshToonMaterial({shininess: 50,emissiveIntensity:0.2,lightMapIntensity:0.4,emissive:color,color:color})
      mat.transparent=true;
      mat.opacity=0.4;
    
    }
  
    mat.depthWrite =true;
    mat.vertexColors=true;//bijna zeker
    mat.side=THREE.DoubleSide;
  return mat;
}


    onLoadReady()
    {
      var group=this.ifcdb.rootGroup;
      for (var i=group.children.length-1; i >= 0; --i)            
    group.remove(group.children[i]);

       
       
        for (var c in this.color_nodes)
        {
            var ar =this.color_nodes[c];
            var cchild2=new Mesh();

                                    cchild2.geometry=mergeGeometries(ar);
                                    cchild2.geometry.applyMatrix4(new Matrix4().makeScale(-1, 1, 1));//flip the normals
           // cchild2.geometry=BufferGeometryUtils.mergeBufferGeometries(ar);

        
            cchild2.material=this.getMat(c,"greytransparent");

            this.ifcdb.rootGroup.add(cchild2);

        }
        
       // this.rootGroup.add(o3d);


        console.log("onLoadReady");
     


    }
}